/**
 * 
 * vuexauthauthListcode
 */
import Vue from 'vue';
import store from '../../store';
/**
 * dom
 * @param {*} el dom
 * @param {*} binding 
 */

function has(el, binding) {
  var value = binding.value;
  var authList = store.getters && store.getters.authList || [];

  if (value && value instanceof Array && value.length > 0) {
    var permissionrules = value;
    var hasPermission = authList.some(function (role) {
      return permissionrules.indexOf(role) >= 0;
    });

    if (!hasPermission && value && value.length) {
      el.parentNode && el.parentNode.removeChild(el);
    }
  }
}

var auth = {
  inserted: has,
  update: has
};

var install = function install(Vue) {
  Vue.directive('auth', auth);
};

Vue.use(install);